import React from 'react'

const EachMusicContainer = ({music,playThisSong}) => {
    
    return (
        <div className='each_music_container' id={music.id} onClick={()=>playThisSong(music.id)}>
            <img className='img' src={music.img} alt=''/>
            <div className='details'>
                <h2 className='song_name'>{music.name}</h2>
                <h3 className='artist_name'>{music.artist}</h3>
            </div>
        </div>
    )
}

export default EachMusicContainer
