import React from 'react'
import music_data from "./music_data"
import EachMusicContainer from './EachMusicContainer'

const MusicListDisplayer = ({playThisSong}) => {
    return (
        
          <section className="music_list_displayer">
              {
                   music_data.map(music=><EachMusicContainer music={music} playThisSong={playThisSong} key={music.id}/>)
               }
          </section>  
        
    )
}

export default MusicListDisplayer
